@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rajdhani:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Concert+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yeon+Sung&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lemonada:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Da+2:wght@400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jura:wght@300;400;500;600;700&family=Unica+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Marmelad&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Averia+Serif+Libre:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Days+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Exo+2:wght@500;600;700;800&display=swap);
body {
  position: fixed;
  width: 100%; height: 100%;
  background: rgba(235, 235, 235, 0.25);

  display: grid;

  grid-template-columns: 100%;
  grid-template-rows: 100%;
}
 * { margin: 0; padding: 0; font-family: Quicksand; outline: none; }

#root {
  grid-column: 1;
  grid-row: 1;

  width: 100%; height: 100%;
  display: grid;
}

/*  warn  */
.Point_warn__2CmMC {
  position: fixed;
  left: 15px; bottom: 10px;
  min-width: 100px;
  min-height: 25px;
  display: none;
  background-color: rgb(255, 149, 144);
  border-radius: 5px;
  box-shadow: 0 0 5px rgb(255, 149, 144);
  padding: 15px 30px;
}
 .Point_warn__2CmMC>span {
   font-family: Dosis;
   font-weight: 600;
   font-size: 16px;
   color: rgb(255, 241, 241);
   letter-spacing: 2px;
 }

/* home label */

.Point_label__3EJW2 {
  width: 100%; height: 100%;
  background-color: #FFF;
}
 .Point_label-btn__uiID4 {
   width: 125px; height: 125px;
   position: fixed;
   background-color: rgb(255, 245, 85);
   border: none;
   border-radius: 25%;
   box-shadow: 0 0 7px rgba(255, 238, 0, 1);
   top: 30%;
   margin: auto calc(50% - 62.5px);
   cursor: pointer;
   z-index: 0;
 }
  .Point_label-btn__uiID4>span {
    font-family: 'Concert One';
    font-weight: 600;
    font-size: 25px;
    letter-spacing: 2px;
    color: #c3a720;
  }
 
 .Point_by__2a4vH {
   position: fixed;
   left: 25px;
   bottom: 15px;
   font-family: Dosis;
   font-weight: 600;
   font-size: 16px;
   color: #333;
   letter-spacing: 2px;
 }


/*  join  */

.Point_join__2aLMV {
    background: #FFF;
    width: 320px; height: 320px;
  
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(200, 200, 200, 0.2);
  
    display: grid;
  
    grid-template-columns: 100%;
    grid-template-rows: 50% 50%;
  
    align-items: center;
    justify-items: center;
  
    align-self: center;
    justify-self: center;
  }
  
  .Point_inp__eVCcy {
    grid-column: 1;
    grid-row: 1;
  
    width: 65%; height: 45%;
  
    background: rgba(200, 200, 200, 0.20);
    border: none;
    border-radius: 10px;
  
    font-family: 'Baloo Da 2';
    font-weight: 500;
    font-size: 18px;
    color: #303030;
  
    padding: 0 15px;
  
    align-self: center;
    justify-self: center;
  }
  
  .Point_next__3yYjM {
    grid-column: 1;
    grid-row: 2;
  
    width: 50%; height: 30%;
    min-width: 130px; min-height: 60px;
  
    background: rgba(255, 238, 0, 0.7);
    border: none;
    border-radius: 10px;
    box-shadow: 0 0 2px #ffff66;
  
    display: grid;
  
    align-items: center;
    justify-items: center;
  
    transition-property: background-color transform;
    transition-duration: .5s;
  }
  .Point_next__3yYjM:hover {
    background-color: rgba(255, 238, 0, 1);
    transition-property: background-color;
    transition-duration: .5s;
  }
  
  .Point_next__3yYjM:active {
    background-color: rgba(255, 238, 0, 1);
    transform: scale(0.9);
  
    transition-property: transform;
    transition: .1s;
  }
   .Point_next__3yYjM>span {
     height: 30px; width: 87px;
     font-family: 'Concert One';
     font-weight: 1000;
     font-size: 25px;
     color: #c3a720;
     
     text-transform: uppercase;
     letter-spacing: 10px;
   }
  
  @media (max-width: 500px) {
  
    body { background: #FFF; }
  
    .Point_join__2aLMV {
      box-shadow: none;
    }
  }

/*  terms of use  */

.Point_terms-of-use__174KA {
  grid-column: 1;
  grid-row: 1;
  
  width: 100%; height: 100%;
  min-width: 320px; min-height: 500px;
  max-width: 475px; max-height: 600px;

  background: #FFF;
  border-radius: 15px;
  box-shadow: 0 0 15px rgba(200, 200, 200, 0.2);

  display: grid;

  grid-template-columns: 100%;
  grid-template-rows: 12% 70% 18%;

  align-self: center;
  justify-self: center;
}

.Point_head__17tLd {
  grid-column: 1;
  grid-row: 1;

  width: 100%; height: 100%;

  font-family: 'Averia Serif Libre';
  font-weight: 600;
  font-size: 30px;
  color: #353535;

  display: grid;

  align-items: end;
  justify-items: center;
}

.Point_rules__2ojn3 {
  grid-column: 1;
  grid-row: 2;

  width: 80%; height: 95%;
  background: rgba(235, 235, 235, 0.2);
  box-shadow: 0 0 3.5px rgba(200, 200, 200, 0.35) inset;
  border-radius: 6px;

  display: grid;

  align-self: center;
  justify-self: center;

  align-items: center;
  justify-items: center;
}
 .Point_rulInner__2cOip {
   width: 80%; height: 90%;

   display: grid;

   grid-template-columns: 100%;
   grid-template-rows: 30% auto;

   align-self: end;
   justify-self: center;
 }
  .Point_rulIf__1Xlzj {
    color: #707070;
  }

  .Point_terms-of-use-read-rule__2NcrU {
    width: 150px; height: 40px;

    border: 2px solid rgba(200, 200, 200, 1);
    border-radius: 5px;

    font-family: Rajdhani;
    font-weight: 600;
    font-size: 16px;
    color: #909090;

    transition: .5s;

    display: grid;

    align-items: center;
    justify-items: center;
  }
   .Point_terms-of-use-read-rule__2NcrU>span {
     font-family: Rajdhani;
     font-weight: 600;
     font-size: 16px;
     color: #909090;

     transition: transform .1s;
   }
   .Point_terms-of-use-read-rule__2NcrU>span:active {
     transform: scale(0.8);
     transition: transform .1s;
   }

.Point_footer__1tYdY {
  grid-column: 1;
  grid-row: 3;

  width: 65%; height: 100%;
  min-width: 260px;

  display: grid;

  grid-template-columns: auto;
  grid-template-rows: 100%;

  align-self: center;
  justify-self: center;
}
 .Point_accept__3LjQ2 {
   grid-column: 1;
   grid-row: 1;

   min-width: 120px; min-height: 50px;
   max-width: 120px; max-height: 50px;

   background: rgba(0, 209, 230, 0.8);
   border: none;
   border-radius: 10px;

   font-family: Quicksand;
   font-weight: bold;
   font-size: 20px;
   color: white;

   display:  grid;

   align-content: center;
   justify-content: center;

   align-self: center;
   justify-self: end;

   transition-property: background-color;
   transition-duration: .5s;
 }
  .Point_accept__3LjQ2:hover {
    background: rgba(0, 209, 230, 1);

    transition-property: background-color;
    transition-duration: .5s;
  }
  .Point_accept__3LjQ2:active {
    background: rgba(0, 209, 230, 1);
    transform: scale(0.9);

    transition-property: transform;
    transition-duration: .1s;
  }

 .Point_deny__1yHuA {
   grid-column: 2;
   grid-row: 1;

   min-width: 90px; min-height: 50px;
   max-width: 90px; max-height: 50px;
   background: rgba(255, 55, 46, 0.85);
   border: none;
   border-radius: 10px;

   font-family: Quicksand;
   font-weight: bold;
   font-size: 20px;
   color: white;

   display: grid;

   align-content: center;
   justify-content: center;

   align-self: center;
   justify-self: end;

   transition-property: background-color;
   transition-duration: .5s;
 }
  .Point_deny__1yHuA:hover {
    background: rgba(255, 55, 46, 1);

    transition-property: background-color;
    transition-duration: .5s;
  }
  .Point_deny__1yHuA:active {
    background: rgba(255, 55, 46, 1);
    transform: scale(0.9);

    transition-property: transform;
    transition-duration: .1s;
  }

@media (max-width: 500px) {

  body { background: #FFF; }

  .Point_terms-of-use__174KA { box-shadow: none; }
}


 /* done */

.Point_done__266C4 {
  background: #FFF;
    width: 320px; height: 320px;
  
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(200, 200, 200, 0.2);
  
    display: grid;
  
    grid-template-columns: 100%;
    grid-template-rows: 50% 50%;
  
    align-items: center;
    justify-items: center;
  
    align-self: center;
    justify-self: center;
}
 .Point_done__266C4>span {
   font-family: 'Concert One';
   font-weight: 600;
   font-size: 20px;
   
   letter-spacing: 10px;
   text-transform: uppercase;
 }

 .Point_yourdata__236HL {
   width: 75%; height: 75%;
   align-self: end;
   justify-self: center;
   display: grid;
   grid-template-columns: 100%;
   grid-template-rows: 50% 50%;
   align-items: center;
   justify-items: center;
 }
  .Point_yourdata__236HL>span {
    font-family: Dosis;
    font-weight: 600;
    font-size: 20px;
    color: #000;
    letter-spacing: 2px;
    align-self: start;
  }

  .Point_yourdata__236HL>div {
    width: auto; height: auto;
    padding: 5px 10px;
    background-color: #eee;
    border-radius: 5px;
  }
   .Point_yourdata__236HL>div>span {
     font-family: 'Baloo Da 2';
     font-weight: 600;
     font-size: 16px;
     color: #333;
     letter-spacing: 1.5px;
   }

   .Point_yourdata__236HL>div>span:nth-last-of-type(2) {
     padding: 0 2px;
     font-family: Quicksand;
     color: #000;
   }
   .Point_yourdata__236HL>div>span:nth-last-of-type(1) {
    font-family: Dosis;
  }

 .Point_done__266C4>button {
   width: 25%; height: 30%;
   min-width: 100px; min-height: 45px;

   background: rgba(255, 238, 0, 0.75);
   border: none;
   border-radius: 5px;

   transition-property: background-color transform;
   transition-duration: .5s;
 }

 .Point_done__266C4>button:hover {
   background-color: rgba(255, 238, 0, 1);
   transition: background-color .5s;
 }

 .Point_done__266C4>button:active {
   background-color: rgba(255, 238, 0, 1);
   transform: scale(0.9);
   transition: transform .1s;
 }
  .Point_done__266C4>button>span {
    font-family: 'Concert One';
    font-weight: 600;
    font-size: 16px;
    color: #c3a720;
    
    letter-spacing: 2px;
    text-transform: uppercase;
  }

@media (max-width: 500px) {
  
  .Point_done__266C4 {
    box-shadow: none;
  }
}
