/*  warn  */
.warn {
  position: fixed;
  left: 15px; bottom: 10px;
  min-width: 100px;
  min-height: 25px;
  display: none;
  background-color: rgb(255, 149, 144);
  border-radius: 5px;
  box-shadow: 0 0 5px rgb(255, 149, 144);
  padding: 15px 30px;
}
 .warn>span {
   font-family: Dosis;
   font-weight: 600;
   font-size: 16px;
   color: rgb(255, 241, 241);
   letter-spacing: 2px;
 }

/* home label */

.label {
  width: 100%; height: 100%;
  background-color: #FFF;
}
 .label-btn {
   width: 125px; height: 125px;
   position: fixed;
   background-color: rgb(255, 245, 85);
   border: none;
   border-radius: 25%;
   box-shadow: 0 0 7px rgba(255, 238, 0, 1);
   top: 30%;
   margin: auto calc(50% - 62.5px);
   cursor: pointer;
   z-index: 0;
 }
  .label-btn>span {
    font-family: 'Concert One';
    font-weight: 600;
    font-size: 25px;
    letter-spacing: 2px;
    color: #c3a720;
  }
 
 .by {
   position: fixed;
   left: 25px;
   bottom: 15px;
   font-family: Dosis;
   font-weight: 600;
   font-size: 16px;
   color: #333;
   letter-spacing: 2px;
 }


/*  join  */

.join {
    background: #FFF;
    width: 320px; height: 320px;
  
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(200, 200, 200, 0.2);
  
    display: grid;
  
    grid-template-columns: 100%;
    grid-template-rows: 50% 50%;
  
    align-items: center;
    justify-items: center;
  
    align-self: center;
    justify-self: center;
  }
  
  .inp {
    grid-column: 1;
    grid-row: 1;
  
    width: 65%; height: 45%;
  
    background: rgba(200, 200, 200, 0.20);
    border: none;
    border-radius: 10px;
  
    font-family: 'Baloo Da 2';
    font-weight: 500;
    font-size: 18px;
    color: #303030;
  
    padding: 0 15px;
  
    align-self: center;
    justify-self: center;
  }
  
  .next {
    grid-column: 1;
    grid-row: 2;
  
    width: 50%; height: 30%;
    min-width: 130px; min-height: 60px;
  
    background: rgba(255, 238, 0, 0.7);
    border: none;
    border-radius: 10px;
    box-shadow: 0 0 2px #ffff66;
  
    display: grid;
  
    align-items: center;
    justify-items: center;
  
    transition-property: background-color transform;
    transition-duration: .5s;
  }
  .next:hover {
    background-color: rgba(255, 238, 0, 1);
    transition-property: background-color;
    transition-duration: .5s;
  }
  
  .next:active {
    background-color: rgba(255, 238, 0, 1);
    transform: scale(0.9);
  
    transition-property: transform;
    transition: .1s;
  }
   .next>span {
     height: 30px; width: 87px;
     font-family: 'Concert One';
     font-weight: 1000;
     font-size: 25px;
     color: #c3a720;
     
     text-transform: uppercase;
     letter-spacing: 10px;
   }
  
  @media (max-width: 500px) {
  
    body { background: #FFF; }
  
    .join {
      box-shadow: none;
    }
  }

/*  terms of use  */

.terms-of-use {
  grid-column: 1;
  grid-row: 1;
  
  width: 100%; height: 100%;
  min-width: 320px; min-height: 500px;
  max-width: 475px; max-height: 600px;

  background: #FFF;
  border-radius: 15px;
  box-shadow: 0 0 15px rgba(200, 200, 200, 0.2);

  display: grid;

  grid-template-columns: 100%;
  grid-template-rows: 12% 70% 18%;

  align-self: center;
  justify-self: center;
}

.head {
  grid-column: 1;
  grid-row: 1;

  width: 100%; height: 100%;

  font-family: 'Averia Serif Libre';
  font-weight: 600;
  font-size: 30px;
  color: #353535;

  display: grid;

  align-items: end;
  justify-items: center;
}

.rules {
  grid-column: 1;
  grid-row: 2;

  width: 80%; height: 95%;
  background: rgba(235, 235, 235, 0.2);
  box-shadow: 0 0 3.5px rgba(200, 200, 200, 0.35) inset;
  border-radius: 6px;

  display: grid;

  align-self: center;
  justify-self: center;

  align-items: center;
  justify-items: center;
}
 .rulInner {
   width: 80%; height: 90%;

   display: grid;

   grid-template-columns: 100%;
   grid-template-rows: 30% auto;

   align-self: end;
   justify-self: center;
 }
  .rulIf {
    color: #707070;
  }

  .terms-of-use-read-rule {
    width: 150px; height: 40px;

    border: 2px solid rgba(200, 200, 200, 1);
    border-radius: 5px;

    font-family: Rajdhani;
    font-weight: 600;
    font-size: 16px;
    color: #909090;

    transition: .5s;

    display: grid;

    align-items: center;
    justify-items: center;
  }
   .terms-of-use-read-rule>span {
     font-family: Rajdhani;
     font-weight: 600;
     font-size: 16px;
     color: #909090;

     transition: transform .1s;
   }
   .terms-of-use-read-rule>span:active {
     transform: scale(0.8);
     transition: transform .1s;
   }

.footer {
  grid-column: 1;
  grid-row: 3;

  width: 65%; height: 100%;
  min-width: 260px;

  display: grid;

  grid-template-columns: auto;
  grid-template-rows: 100%;

  align-self: center;
  justify-self: center;
}
 .accept {
   grid-column: 1;
   grid-row: 1;

   min-width: 120px; min-height: 50px;
   max-width: 120px; max-height: 50px;

   background: rgba(0, 209, 230, 0.8);
   border: none;
   border-radius: 10px;

   font-family: Quicksand;
   font-weight: bold;
   font-size: 20px;
   color: white;

   display:  grid;

   align-content: center;
   justify-content: center;

   align-self: center;
   justify-self: end;

   transition-property: background-color;
   transition-duration: .5s;
 }
  .accept:hover {
    background: rgba(0, 209, 230, 1);

    transition-property: background-color;
    transition-duration: .5s;
  }
  .accept:active {
    background: rgba(0, 209, 230, 1);
    transform: scale(0.9);

    transition-property: transform;
    transition-duration: .1s;
  }

 .deny {
   grid-column: 2;
   grid-row: 1;

   min-width: 90px; min-height: 50px;
   max-width: 90px; max-height: 50px;
   background: rgba(255, 55, 46, 0.85);
   border: none;
   border-radius: 10px;

   font-family: Quicksand;
   font-weight: bold;
   font-size: 20px;
   color: white;

   display: grid;

   align-content: center;
   justify-content: center;

   align-self: center;
   justify-self: end;

   transition-property: background-color;
   transition-duration: .5s;
 }
  .deny:hover {
    background: rgba(255, 55, 46, 1);

    transition-property: background-color;
    transition-duration: .5s;
  }
  .deny:active {
    background: rgba(255, 55, 46, 1);
    transform: scale(0.9);

    transition-property: transform;
    transition-duration: .1s;
  }

@media (max-width: 500px) {

  body { background: #FFF; }

  .terms-of-use { box-shadow: none; }
}


 /* done */

.done {
  background: #FFF;
    width: 320px; height: 320px;
  
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(200, 200, 200, 0.2);
  
    display: grid;
  
    grid-template-columns: 100%;
    grid-template-rows: 50% 50%;
  
    align-items: center;
    justify-items: center;
  
    align-self: center;
    justify-self: center;
}
 .done>span {
   font-family: 'Concert One';
   font-weight: 600;
   font-size: 20px;
   
   letter-spacing: 10px;
   text-transform: uppercase;
 }

 .yourdata {
   width: 75%; height: 75%;
   align-self: end;
   justify-self: center;
   display: grid;
   grid-template-columns: 100%;
   grid-template-rows: 50% 50%;
   align-items: center;
   justify-items: center;
 }
  .yourdata>span {
    font-family: Dosis;
    font-weight: 600;
    font-size: 20px;
    color: #000;
    letter-spacing: 2px;
    align-self: start;
  }

  .yourdata>div {
    width: auto; height: auto;
    padding: 5px 10px;
    background-color: #eee;
    border-radius: 5px;
  }
   .yourdata>div>span {
     font-family: 'Baloo Da 2';
     font-weight: 600;
     font-size: 16px;
     color: #333;
     letter-spacing: 1.5px;
   }

   .yourdata>div>span:nth-last-of-type(2) {
     padding: 0 2px;
     font-family: Quicksand;
     color: #000;
   }
   .yourdata>div>span:nth-last-of-type(1) {
    font-family: Dosis;
  }

 .done>button {
   width: 25%; height: 30%;
   min-width: 100px; min-height: 45px;

   background: rgba(255, 238, 0, 0.75);
   border: none;
   border-radius: 5px;

   transition-property: background-color transform;
   transition-duration: .5s;
 }

 .done>button:hover {
   background-color: rgba(255, 238, 0, 1);
   transition: background-color .5s;
 }

 .done>button:active {
   background-color: rgba(255, 238, 0, 1);
   transform: scale(0.9);
   transition: transform .1s;
 }
  .done>button>span {
    font-family: 'Concert One';
    font-weight: 600;
    font-size: 16px;
    color: #c3a720;
    
    letter-spacing: 2px;
    text-transform: uppercase;
  }

@media (max-width: 500px) {
  
  .done {
    box-shadow: none;
  }
}