body {
  position: fixed;
  width: 100%; height: 100%;
  background: rgba(235, 235, 235, 0.25);

  display: grid;

  grid-template-columns: 100%;
  grid-template-rows: 100%;
}
 * { margin: 0; padding: 0; font-family: Quicksand; outline: none; }

#root {
  grid-column: 1;
  grid-row: 1;

  width: 100%; height: 100%;
  display: grid;
}
